.messenger {
     position: absolute;
     top: 0;
     right: 0;
     width: 300px;
     background: white;
     height: calc(100vh - 79px);
     box-sizing: border-box;
     box-shadow: 0px 0px 20px 2px rgba(146, 146, 146, 75%);

     z-index: 1;
     .header {
          padding: 10px 20px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          height: 50px;
          margin-bottom: 20px;

          h3 {
               margin: 0;
               font-weight: 400;
          }
          .icon {
               cursor: pointer;
               font-size: 20px;
          }
     }
     .header_tab {
          display: flex;
          justify-content: space-between;
          align-items: center;
          border-bottom: 1px solid #eee;
          margin-bottom: 20px;
          .tab {
               display: flex;
               align-items: center;
               justify-content: center;
               width: 100%;
               color: #555;
               padding: 15px;
               p {
                    margin-left: 10px;
               }
               &.active {
                    border-bottom: 1px solid #04796a;
                    color: #04796a;
               }
               &:hover {
                    cursor: pointer;
                    background: rgba(0, 121, 107, 0.039);
               }
          }
     }
     .chat-section {
          padding: 20px;
          display: flex;

          text-align: left;
          overflow-y: scroll;

          height: calc(100vh - 340px);
          @media (max-width: 768px) {
               height: calc(100vh - 470px);
          }
          .sender {
               font-weight: bold;
          }
          .message {
               font-weight: 400;
               padding: 0;
               margin: 5px 0px;
               &:before {
                    content: "├──";
               }
          }
     }
     .send-msg-section {
          display: flex;
          justify-content: center;
          align-items: center;

          input.msginput {
               height: 30px;
               padding: 10px;
               width: 200px;
               border: 1px solid #e3e3e3;
               &:focus {
                    outline: none;
                    border: 1px solid #aaaaaa;
               }
          }
          input.msginput:focus ~ .floating-label,
          input.msginput:not(:focus):valid ~ .floating-label {
               padding: 19px;
               font-weight: 300;
               height: 30px;
               bottom: 30px;
               left: 5px;
               font-size: 11px;
               opacity: 1;
               color: #cccccc;
               @media (max-width: 768px) {
                    bottom: 160px;
               }
          }

          .floating-label {
               font-size: 13px;
               color: #b4b2b2;
               position: absolute;
               pointer-events: none;
               bottom: 45px;
               left: 25px;
               transition: all 0.1s ease;
               @media (max-width: 768px) {
                    bottom: 174px;
               }
          }

          .icon {
               font-size: 28px;
               padding: 10px;
               cursor: pointer;
               color: #e3e3e3;
               &:hover {
                    color: #1a73e8;
               }
          }

          input.msginput:focus ~ .icon {
               color: #1a73e8;
               &:hover {
                    color: #1a73e8;
               }
          }
     }
}
